@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;900&display=swap);
* {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}
body {
  background-color: rgb(163, 165, 250);
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 15rem;
}

.box {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  width: 300px;
  min-height: 100px;
  padding: 1rem;
  border-radius: 5px;
  box-shadow: 5px 27px 20px 5px rgba(0, 0, 0, 0.12);
  font-size: 10px;
  margin-top: -3rem;
}

@media (min-width: 400px) {
  .box {
    width: 400px;
    min-height: 100px;
    padding: 1rem;
    border-radius: 5px;
    box-shadow: 5px 27px 20px 5px rgba(0, 0, 0, 0.12);
    font-size: 12px;
    margin-top: 3rem;
  }
}

@media (min-width: 600px) {
  .box {
    width: 600px;
    min-height: 100px;
    padding: 1rem;
    border-radius: 5px;
    box-shadow: 5px 27px 20px 5px rgba(0, 0, 0, 0.12);
  }
}

